export interface ICreatePersonnelRequest {
  name: string;
  phone_number: string;
  date_of_birth: string;
  image_url?: string;
}

export type IUpdatePersonnelRequest = Partial<ICreatePersonnelRequest>;

export interface IPersonnelResponse {
  id: string;
  name: string;
  phone_number: string;
  email: string;
  date_of_birth: string;
  image_url?: string;
  user_type: string;
}
